$enable-transitions : true !default;
$primary : inherit !default;

$primary : #000 !default; 


$transition-base:             all .2s ease-in-out !default;
$transition-fade:             opacity .15s linear !default;
$transition-collapse:         height .35s ease !default;


.rounded-circle {
    border-radius: 50% !important;
  }
   %color-primary {
       color:$primary;
   }
   %color-secondary {
        color: $primary;
   }
  
  %background-color-primary {
      background: $primary;
  }
  %background-color-secondary {
      background: $primary;
  }
  %color-body {
      color: inherit;
  }
  $white1 : #f1f1f1 !default; 
  $black1 : #999 !default; 
  $black2 : #999 !default; 
  $gray10: #CCC !default; 
  $grid-gutter-width: 15 !default; 
  $border-color : #f2f2f2 !default; 
  $gray1 : #CCC !default;
  $gray13 : #CCC !default;
  $secondary : $primary ;
  $transparent: none !default;
  